body {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

.header {
  height: 80px;
  background: linear-gradient(to right, #ffafbd, #ffc3a0);
  justify-content: center;
  align-items: center;
  display: flex;
}

.header h1 {
  color: #fff;
  text-transform: uppercase;
  margin: 0;
  font-family: Montserrat, sans-serif;
  font-size: 36px;
  font-weight: bold;
}

.header-container {
  width: 90%;
  max-width: 1200px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.search-form {
  background-color: #f5f5f5;
  border: 1px solid #ddd;
  border-radius: 20px;
  align-items: center;
  padding: 10px 20px;
  display: flex;
}

.search-form input[type="text"] {
  width: 100%;
  color: #292929;
  border: none;
  margin-right: 10px;
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  font-weight: bold;
}

.search-form input[type="text"] ::-ms-input-placeholder {
  color: #292929;
}

.search-form input[type="text"] ::placeholder {
  color: #292929;
}

.search-form input[type="text"]:focus {
  outline: none;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #ffafbd;
  background-image: linear-gradient(to right, #ffafbd, #ffc3a0);
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  font-weight: bold;
  transition: all .2s ease-in-out;
}

.search-form button[type="submit"]:hover {
  transform: scale(1.1);
}

main {
  background-color: #f9f9f9;
}

main .gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 50px;
  padding: 0;
  display: flex;
}

main .card {
  cursor: pointer;
  border-radius: 15px;
  margin-bottom: 50px;
}

main .card:hover {
  box-shadow: 3px 3px 5px -1px rgba(119, 119, 119, .75);
}

main .card-img {
  object-fit: cover;
  width: 300px;
  height: 200px;
  border-radius: 15px 15px 0 0;
}

main .info {
  flex-direction: row;
  gap: 10px;
  display: flex;
}

main .info-item {
  color: #292929;
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 20px;
  display: flex;
}

